import TextInput from '../../../components/Inputs/TextInput/TextInput';
import { useEffect, useState } from 'react'
import MoneyInput from '../../../components/Inputs/MoneyInput/MoneyInput';
import CommentInput from '../../../components/Inputs/CommentInput/CommentInput';
import { ADD_BOOK, AUTHORS, UPDATE_BOOK } from './query';
import { useMutation, useQuery } from '@apollo/client/react';
import DropSearchInput from '../../../components/Inputs/DropSearchInput/DropSearchInput';
import { GENERE } from '../../../pages/AdminGenre/query';
import Loader from '../../../components/Loader/Loader';
import FotoInput from '../../../components/Inputs/FotoInput/FotoInput';

const AdminAddBook = ({ setOpenForm, info, setFunk }) => {
   const [bookName, setBookName] = useState(info && info?.bookName)
   const [authorName, setAuthorName] = useState(info && info?.author?.Id)
   const [price, setPrice] = useState(info && `${info?.bookPrice}`)
   const [discount, setDiscount] = useState(info ? info?.discount : 0)
   const [about, setAbout] = useState(info && info?.bookDescription)
   const [kitobdan, setKitobdan] = useState(info && info?.bookExcerpt)
   const [forWhom, setForWhom] = useState(info && info?.forWhom)
   const [publishingHouse, setPublishingHouse] = useState(info && info?.bookName)
   const [year, setYear] = useState(info && info?.bookRelease)
   const [numberOfPages, setNumberOfPages] = useState(info && info?.bookPages)
   const [format, setFormat] = useState(info && info?.format)
   const [weight, setWieght] = useState(info && info?.bookWeight)
   const [img, setImg] = useState({
      imgURL: info ? info?.bookImage : null,
      img:  undefined
   })
   const [genere, setGenere] = useState(info && info?.genre?.Id)

   const [arrGenere, setArrGenere] = useState()
   const [arrAuthors, setArrAuthors] = useState()

   const [createBook, {data: create, loading: crLoading}] = useMutation(ADD_BOOK)
   const [updateBook, {data: update, loading: upLoading}] = useMutation(UPDATE_BOOK)
   const {data: generes} = useQuery(GENERE)
   const {data: authors} = useQuery(AUTHORS)

   const submit = (e) => {
      e.preventDefault()
      if (info && bookName && authorName && price && about && genere && numberOfPages){
         updateBook({
            variables: {
               ID: info?.Id,
               name: bookName,
               price: +price,
               description: about,
               publishHouse: publishingHouse,
               release: +year,
               pages: +numberOfPages,
               weight: +weight,
               image: img?.img,
               excerpt: kitobdan,
               author: authorName,
               genre: genere,
               discount: +discount || 0,
               forWhom: forWhom,
               format: format
            }
         })
      }
      else if (!info && img?.img && bookName && authorName && price && about && genere && numberOfPages) {
         createBook({
            variables: {
               name: bookName,
               price: +price,
               description: about,
               publishHouse: publishingHouse,
               release: +year,
               pages: +numberOfPages,
               weight: +weight,
               image: img?.img,
               excerpt: kitobdan,
               author: authorName,
               genre: genere,
               discount: +discount || 0,
               forWhom: forWhom,
               format: format
            }
         })
      }else {
         alert("Formani to'liq to'ldiring")
      }
      
   }

   useEffect(() => {
      if (generes && generes?.genres?.success) {
         setArrGenere(generes?.genres?.data)
      }
   }, [generes])
   useEffect(() => {
      if (authors && authors?.authors?.success) {
         setArrAuthors(authors?.authors?.data)
      }
   }, [authors])

   useEffect(() => {
      if (create && create?.createBook?.success) {
         setFunk()
         setOpenForm(false)
      } else if (update && update?.updateBook?.success) {
         setFunk()
         setOpenForm(false)
      }
   }, [create, setFunk, setOpenForm, update])


   return (
      <div className="pageForm-right">
         {
            (crLoading || upLoading) && <Loader/>
         }
         <div className="pageForm-right-top">
            <h3 className="pageForm-right-top__title">
               Add new book
            </h3>

            <button className="pageForm-right-top__button"
               onClick={() => {
                  setOpenForm(false)
               }}
            >
               <span></span>
            </button>
         </div>
         <form className="pageForm-right-center"
            onSubmit={submit}
         >

            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Book img
               </label>
               <FotoInput setImg={setImg} img={img}/>
            </div>
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Book name
               </label>
               <TextInput defaultInfo={bookName} setInfo={setBookName} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Janr
               </label>
               <DropSearchInput arr={arrGenere} fnc={setGenere} teach={true} defolt={genere} />
               
            
            </div>
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Author name
               </label>
              <DropSearchInput arr={arrAuthors} fnc={setAuthorName} teach={true} defolt={authorName} />
               
              
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Price $
               </label>
               <MoneyInput defaultInfo={price} setInfo={setPrice} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  A discount Price $
               </label>
               <MoneyInput defaultInfo={discount} setInfo={setDiscount} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  About what
               </label>
               <CommentInput defaultInfo={about} setInfo={setAbout} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Kitobdan parcha
               </label>
               <CommentInput defaultInfo={kitobdan} setInfo={setKitobdan} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  For whom
               </label>
               <CommentInput defaultInfo={forWhom} setInfo={setForWhom} />
            </div>


            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Publishing house (max length: 32)
               </label>
               <TextInput defaultInfo={publishingHouse} setInfo={setPublishingHouse} max={32}/>
            </div>
           
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Year of release 
               </label>
               <TextInput defaultInfo={year} setInfo={setYear} type={`number`} max={4}/>
            </div>
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Number of pages
               </label>
               <TextInput defaultInfo={numberOfPages} setInfo={setNumberOfPages} type={`number`} />
            </div>
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Format
               </label>
               <TextInput defaultInfo={format} setInfo={setFormat} />
            </div>

            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Wieght
               </label>
               <TextInput defaultInfo={weight} setInfo={setWieght} type={`number`} />
            </div>



            <button className='pageForm-right-center__button' type='submit'>Save</button>


         </form>
      </div>
   )
}

export default AdminAddBook