import {
	gql
} from "@apollo/client/core";

const ADD_BOOK = gql `
	mutation createBook(
		$image: Upload!
		$name: String!
		$price: Int!
		$description: String!
		$publishHouse: String
		$release: Int
		$pages: Int!
		$weight: Int
		$author: String
		$genre: String!
		$excerpt: String
		$discount: Int
		$forWhom: String,
		$format: String
	) {
		createBook(
			image: $image
			name: $name
			price: $price
			description: $description
			publishHouse: $publishHouse
			release: $release
			pages: $pages
			weight: $weight
			author: $author
			genre: $genre
			excerpt: $excerpt
			discount: $discount
			forWhom: $forWhom
			format: $format
		) {
			success
			message
		}
	}

`

const UPDATE_BOOK = gql `
	mutation updateBook(
		$ID: ID!
		$image: Upload
		$name: String
		$price: Int
		$description: String
		$publishHouse: String
		$release: Int
		$pages: Int
		$weight: Int
		$author: String
		$genre: String
		$excerpt: String
		$discount: Int
		$forWhom: String
	) {
		updateBook(
			ID: $ID
			image: $image
			name: $name
			price: $price
			description: $description
			publishHouse: $publishHouse
			release: $release
			pages: $pages
			weight: $weight
			author: $author
			genre: $genre
			excerpt: $excerpt
			discount: $discount
			forWhom: $forWhom
		) {
			success
			message
		}
	}

`




const AUTHORS  = gql `
query {
	authors {
		success
		message
		data {
			...on Author {
				Id
				authorName
			}
		}
	}
}
`

export {
	ADD_BOOK,
	AUTHORS,
	UPDATE_BOOK
}