import {
   gql
} from "@apollo/client/core";

const GET_POPULAR_BOOKS = gql `
   query ($count: Int, $page: Int, $popular: Boolean){
      books(popular: $popular, count: $count, page: $page) {
         pages
         counts
         success
         message
         data {
           ...on Book {
               Id
               bookName
               bookPrice
               author {
                  Id
                  authorName
               }
               genre {
                  Id
                  genreName
               }
               bookImage
               discount
               viewsCount

            }
         }
      }
   }
`

export default GET_POPULAR_BOOKS