
const MoneyInput = ({  setInfo, defaultInfo }) => {
   return (
      <div className="input type1">
         <input type="text" 
            autoComplete="off"
            defaultValue={defaultInfo ? defaultInfo.toString().replace(/ /g, '').replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, " ") : ''}
            onKeyUp={e => {
               e.target.value = e.target.value.replace(/ /g, '').replace(/\D/g, "").replace(/\B(?=(\d{3})+(?!\d))/g, " ")
               setInfo(e.target.value.replace(/ /g, ''))
            }}

         />
      </div>
   )
}


export default MoneyInput