import { useEffect, useState } from 'react';
import BasketBlock from '../../components/BasketBlock/BasketBlock';
import VerifyLoader from '../../components/VerifyLoader/VerifyLoader';
import { useOrders } from '../../contexts/DataProvider';
import './Basket.scss';

const Basket = () => {
   const [orders, setOrders] = useOrders()
   const [total, setTotal] = useState(0)
   const [loader, setLoader] = useState(false)
   const [verify, setVerify] = useState(false)

   // Submit Form

   const submit = async (e) => {
      e.preventDefault()
      let token = `5284569619:AAF5hp2DQurlWnOWbKFjeTBFdsjDnzl-hLA`
      let date = new Date()
      let time = date.getHours() + ":" + date.getMinutes() + ":" + date.getSeconds();
      setLoader(true)
      let response = await fetch(`https://api.telegram.org/bot${token}/sendMessage`, {
         method: 'POST',
         headers: {
            'Content-Type': 'application/json'
         },
         body: JSON.stringify({
            chat_id: '@ziyobooks_basket',
            text: `
                    ziyobooks.com ${date.toLocaleDateString()}
Time: ${time}
Telegram: ${e.target[2].value ? e.target[2].value : 'Malumot kiritilmadi'}
Ismi: ${e.target[0].value}
Telefon: ${e.target[1].value}
Gmail: ${e.target[3].value ? e.target[3].value : 'Malumot kiritilmadi'}
Total price: $${total}
Total books: ${orders?.length}

${orders.map(el => (
               `
Kitob: ${el?.bookName} 
Narxi: $${el?.discount > 0 ? +el?.bookPrice - +el?.discount : el?.bookPrice}
Soni: ${el?.count}
`
            ))}`
         })
      })

      if (await response) {
         setOrders([])
         e.target.reset()
         setLoader(false)
         setVerify(true)
         setTimeout(() => {
            setVerify(false)
         }, 2500);
      }
   }

   useEffect(() => {
      if (orders) {
         let counter = 0
         orders.forEach(el => {
            counter += el.count * el.bookPrice
         })

         setTotal(counter)
      }
   }, [orders])


   return (
      <div className="basket container">
         {
           (loader || verify) && <VerifyLoader loading={loader} verify={verify} />
         }
         <div className="basket-left">
            <div className="basket-left-header">
               <h2 className="basket-left-header__title">Savatchada <span>{orders?.length} ta kitob</span></h2>
               <button className="basket-left-header__clear"
                  onClick={() => {
                     setOrders([])
                  }}
               >
                  Tozalash
               </button>
            </div>

            <div className="basket-left-center">
               {
                  orders && orders.map((el, key) => (
                     <BasketBlock data={el} key={key} />
                  ))
               }

            </div>
         </div>

         {
            orders && orders.length > 0 && <>
               <div className="basket-right">
                  <div className="basket-right-inner">
                     <div className="basket-right-row">
                        <h3 className="basket-right-row__title">Jami:</h3>
                        <h3 className="basket-right-row__subtitle">${total}</h3>
                     </div>
                     <form className="basket-right-form" onSubmit={submit}>

                        <div className="basket-right-form-row">
                           <label htmlFor="">Ism *</label>
                           <input type="text" required />
                        </div>
                        <div className="basket-right-form-row">
                           <label htmlFor="">Telefon raqam *</label>
                           <input type="tel" required />
                        </div>
                        <div className="basket-right-form-row">
                           <label htmlFor="">Telegram</label>
                           <input type="text" placeholder='@masalan' />
                        </div>
                        <div className="basket-right-form-row">
                           <label htmlFor="">Gmail</label>
                           <input type="email" placeholder='masalan@gmail.com' />
                        </div>
                        <div className="basket-right-form-row">
                           <button className="basket-right-form-row__button" type='submit'>
                              Buyurtma berish
                           </button>
                        </div>
                     </form>
                  </div>
               </div>
            </>
         }
         
      </div>
   )
}

export default Basket