import React, { createContext, useContext, useEffect, useState } from 'react'
import Cookies from 'universal-cookie';
const Context = createContext()



const DataProvider = ({ children }) => {
   // eslint-disable-next-line react-hooks/exhaustive-deps
   let cookies = new Cookies();
   
   const [orders, setOrders] = useState(cookies.get('myCat') || [])
   
   
   const value = {
      orders,
      setOrders
   }
   
   useEffect(() => {
      if(orders) {
         cookies.set('myCat', JSON.stringify(orders), { path: '/' });
      }
   }, [cookies, orders])

   return (
      <Context.Provider value={value}>
         <Context.Consumer>
            {
               () => children
            }
         </Context.Consumer>
      </Context.Provider>
   )
}

const useOrders = (setterOnly) => {
   const { orders, setOrders } = useContext(Context)
   return setterOnly ? [setOrders] : [orders, setOrders]
}



export {
   DataProvider,
   useOrders
}