import { useEffect, useRef, useState } from 'react';
import { ArrowDropSearch } from '../../../assets/Icons';
import './DropSearchInput.scss';

const DropSearchInput = ({ arr, fnc, /*notReq,*/ defolt, stGroups, teach, disabled }) => {
   const input = useRef()
   const browsers = useRef()
   const arrow = useRef()
   const [thisValue, setThisValue] = useState()
   
  




   useEffect(() => {
      arrow.current.classList.remove('active')
      browsers.current.style.maxHeight = '0';
      input.current.style.borderRadius = "5px";



      arrow.current.addEventListener('click', () => {
         if (arrow.current.className === 'dropSearchArrow active') {
            arrow.current.classList.remove('active')
            browsers.current.style.maxHeight = '0';
            input.current.style.borderRadius = "5px";

         } else {
            arrow.current.classList.add('active')
            browsers.current.style.maxHeight = '10rem';
            input.current.style.borderRadius = "5px 5px 0 0";

         }
      })




      input.current.onfocus = function () {
         arrow.current.classList.add('active')
         browsers.current.style.maxHeight = '10rem';
         input.current.style.borderRadius = "5px 5px 0 0";


         for (let option of browsers.current.childNodes) {
            if (thisValue === option.dataset.id && thisValue !== undefined) {
               option.classList.add('selected')
            } else {
               option.classList.remove('selected')
            }
         }
      };
      browsers.current.childNodes.forEach(option => {
         option.onclick = function () {
            if (option.className === 'disabled') {

            }
            else if (option.className === 'selected') {
               input.current.value = '';
            } else {
               input.current.value = option.textContent;
            }
            arrow.current.classList.remove('active')
            browsers.current.style.maxHeight = '0';
            input.current.style.borderRadius = "5px";
         }
      })


      input.current.oninput = function () {
         currentFocus = -1;
         var text = input.current.value.toUpperCase();
         for (let option of browsers.current.childNodes) {
            if (option.textContent.toUpperCase().indexOf(text) > -1) {
               option.style.display = "block";
            } else {
               option.style.display = "none";
            }
         };
      }
      var currentFocus = -1;
      input.current.onkeydown = function (e) {
         if (e.keyCode === 40) {
            currentFocus++
            addActive(browsers.current.childNodes);
         }
         else if (e.keyCode === 38) {
            currentFocus--
            addActive(browsers.current.childNodes);
         }
         else if (e.keyCode === 13) {
            e.preventDefault();
            if (currentFocus > -1) {
               /*and simulate a click on the "active" item:*/
               if (browsers.current.options) browsers.current.childNodes[currentFocus].click();
            }
         }
      }

      function addActive(x) {
         if (!x) return false;
         removeActive(x);
         if (currentFocus >= x.length) currentFocus = 0;
         if (currentFocus < 0) currentFocus = (x.length - 1);
         x[currentFocus].classList.add("active");
      }
      function removeActive(x) {
         for (var i = 0; i < x.length; i++) {
            x[i].classList.remove("active");
         }
      }
   }, [arr, thisValue])

   useEffect(() => {
      wrapperRef.current.addEventListener('blur', () => {
         arrow.current.classList.remove('active')
         browsers.current.style.display = 'none';
         input.current.style.borderRadius = "5px";
      })

   }, [])

   function useOutsideAlerter(ref) {
      useEffect(() => {

         function handleClickOutside(event) {
            if (ref.current && !ref.current.contains(event.target)) {
               arrow.current.classList.remove('active')
               browsers.current.style.maxHeight = '0';
               input.current.style.borderRadius = "5px";
            }
         }

         document.addEventListener("mousedown", handleClickOutside);
         return () => {
            document.removeEventListener("mousedown", handleClickOutside);
         };
      }, [ref]);
   }



   const wrapperRef = useRef(null);
   useOutsideAlerter(wrapperRef);


   const [obj, setObj] = useState()

   useEffect(() => {
      if (defolt) {
         if (teach) {
            setObj(arr && arr.find(el => el.Id === defolt))
            setThisValue(defolt)
         } else {
            setObj(arr && arr.find(el => el.id === defolt))
            setThisValue(defolt)
         }
      }

   }, [arr, defolt, teach])


   return (
      <div className="dropSearch" ref={wrapperRef}>
         <div className="inputWrapper" >
            <div className="inputWrr">
               <input
                  defaultValue={defolt ? arr && (obj?.authorName || obj?.genreName) : ''}
                  autoComplete="off" list="" name="browsers" placeholder={`Tanlang`}
                  ref={input}
                  disabled={disabled ? true : false}
               // required={notReq ? false : true}
               />
               <span ref={arrow} className="dropSearchArrow" ><ArrowDropSearch /></span>
            </div>
         </div>
         <div className="dropSearchDatalist" ref={browsers} style={{ maxHeight: '0' }}>
            {
               arr && arr.map((z, i) => (
                  <span
                     disabled={stGroups && stGroups.find(el => el.id === z.id) ? true : false}
                     className={stGroups && stGroups.find(el => el.id === z.id) ? 'disabled' : ''}
                     data-id={teach ? z?.Id : z?.id}
                     onClick={(e) => {
                        if (e.target.className === 'selected') {
                           fnc('')
                           setThisValue()
                        } else {
                           fnc(e.target.dataset.name)
                           setThisValue(e.target.dataset.id)
                        }
                     }}
                     key={i} data-name={z.id ? z.id : z.Id}>{z.authorName ? z.authorName : z.genreName}</span>
               ))
            }
         </div>
      </div>
   )
}

export default DropSearchInput