import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import { BrowserRouter as Router } from 'react-router-dom'
import { ApolloProvider, ApolloClient, InMemoryCache, from } from "@apollo/client";
import { createUploadLink } from "apollo-upload-client";
import { setContext } from '@apollo/client/link/context'
import { onError } from "@apollo/client/link/error";
import { LoginProvider } from './contexts/LoginProvider';
import { DataProvider } from './contexts/DataProvider';

const httpLink = createUploadLink({
  uri: 'https://api.ziyobooks.com/graphql',
})

const authLink = setContext(async (_, {
  headers
}) => {
  const token = await localStorage.getItem('token')
  return {
    headers: {
      ...headers,
      authorization: token ? `${token}` : "",
    }
  }
})

const errorLink = onError(({ graphQLErrors, networkError }) => {
  if (graphQLErrors[0]?.message === "Context creation failed: Authorization is failed!") {
    // window.localStorage.removeItem('token')
    // window.location.reload()
  }
});


const appLink = from([
  errorLink,
  authLink.concat(httpLink)
])

const client = new ApolloClient({
  link: appLink,
  cache: new InMemoryCache()
})



ReactDOM.render(
  <React.StrictMode>
    <LoginProvider>
      <DataProvider>
        <ApolloProvider client={client}>
          <Router>
            <App />
          </Router>
        </ApolloProvider>
      </DataProvider>
    </LoginProvider>
  </React.StrictMode>,
  document.getElementById('root')
);