import { Link } from 'react-router-dom';
import './BookBlock.scss';
import BookAnim from '../../assets/Images/anim.png'
import { BuyIcon, ShareIcon, ViewIcon } from '../../assets/Icons';
import { useState } from 'react';
import { useOrders } from '../../contexts/DataProvider';

const BookBlock = ({ data }) => {
    const [share, setShare] = useState(false)
    const [setOrders] = useOrders(true)
    return (
        <div className="bookBlock">
            <Link to={`/about/${data?.Id}`} className="bookBlock-img" >
                <img src={data?.bookImage ? data?.bookImage : BookAnim} alt="" />
            </Link>

            <div className="bookBlock-info">
                <Link to={`/about/${data?.Id}`} className="bookBlock-info__link">
                    <h4 to={`/about/${data?.Id}`} className="bookBlock-info__title">
                        {data?.bookName}
                    </h4>

                    <span className="bookBlock-info__subtitle">
                        {data?.author?.authorName}
                    </span>

                </Link>

                <div className="bookBlock-info-row">
                    <h4 className="bookBlock-info-row__view">
                        <ViewIcon /> <span className="bookBlock-info-row__view--count">{data?.viewsCount}</span>
                    </h4>
                    <button type='button' className="bookBlock-info-row__share"
                        onClick={() => {
                            navigator.clipboard.writeText(`https://ziyobooks.com/about/${data.Id}`)
                            setShare(true)
                            setTimeout(() => {
                                setShare(false)
                            }, 3000);
                        }}
                    >
                        {
                            share ? `Link nusxalandi` : <ShareIcon />
                        }
                    </button>
                </div>
                <div className="bookBlock-info-row">
                    <button type='button' className="bookBlock-info-row__button"
                        onClick={() => {
                            setOrders((state) => {
                                let find = state.find(el => el.Id === data?.Id)

                                if(find) {
                                    let array = []
                                    find.count = find.count + 1
                                    state.forEach(el => {
                                        if(el.Id === data?.Id) {
                                            array.push(find)
                                        }else {
                                            array.push(el)
                                        }
                                    })
                                    return array
                                }else {
                                    return [...state, {
                                        ...data,
                                        count: 1
                                    }]
                                }
                                
                            })
                        }}
                    >
                        <BuyIcon />
                    </button>

                    <div className="bookBlock-info-row-box">
                        <h4 className="bookBlock-info-row__price">
                            ${data?.discount > 0 ? +data?.bookPrice - +data?.discount : data?.bookPrice}
                        </h4>
                        <h4 className="bookBlock-info-row__price">
                            {
                                data?.discount > 0 && <del>${data?.bookPrice}</del>
                            }
                        </h4>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default BookBlock