import './AuthorBlock.scss';
import { Link } from 'react-router-dom';

const AuthorBlock = ({ img, name,  link}) => {
  return (
    <Link to={`/author/${link}`} className="authorBlock">
      <div className="authorBlock-img">
        <img src={img} alt="" />
      </div>
      <div className="authorBlock-info">
        <h3 className="authorsBlock-info__title">{name}</h3>
      </div>
    </Link>
  )
}

export default AuthorBlock;