import {
   gql
} from "@apollo/client/core";

const GET_GENRE_BOOKS = gql `
   query($ID: ID!) {
      genre(ID: $ID) {
         success
         message
         data {
            ...on Genre {
               Id
               genreName
               books {
                  Id
                  bookName
                  bookPrice
                  bookImage
                  discount
                  author {
                     authorName
                  }
                  discount
                  viewsCount
               }
            }
         }
      }
   }
`

export default GET_GENRE_BOOKS