import './AdminGenre.scss';
import { useQuery } from '@apollo/client/react';
import { GENERE } from './query';
import { useEffect, useState } from 'react';
import AuthorCard from '../../components/AuthorCard/AuthorCard';
import PageForm from '../../containers/PageForm/PageForm';
import Loader from '../../components/Loader/Loader';


const AdminGenre = () => {
   const [genresArr, setGenresArr] = useState()
   const [openForm, setOpenForm] = useState(false)
   const { data: generes, loading } = useQuery(GENERE)
   useEffect(() => {
      if (generes && generes?.genres?.success) {
         setGenresArr(generes?.genres?.data)
         console.log(generes?.genres?.data)
      }
   }, [generes])
   return (
      <div className="adminGenre container">
         <PageForm
            openForm={openForm}
            setOpenForm={setOpenForm}
         />
         {
            loading && <Loader/>
         }
         <div className="adminGenre-top">
            <h2 className="adminAuthors-top__title">
               Genre
            </h2>

            <button className="adminBooks-top__button"
               onClick={() => setOpenForm(true)}
            >
               Genere Add
            </button>
         </div>

         <div className="adminGenre-center">
            {
               genresArr && genresArr.map((el, key) => (
                  <AuthorCard key={key} elem={el} type={`genre`}/>
               ))
            }
         </div>

      </div>
   )
}

export default AdminGenre