import {
   gql
} from "@apollo/client";

const GET_BOOKS = gql `
query books($search: String, $page: Int, $count: Int) {
   books(search: $search, page: $page, count: $count) {
      pages
      counts
      success
      message
      data {
         ...on Book {
            Id
            bookName
            bookPrice
            author {
               Id
               authorName
            }
            genre {
               Id
               genreName
            }
            bookImage
            discount
            viewsCount
           
         }
      }
   }
}
`

export {
   GET_BOOKS
}