import './AuthorsBooks.scss';
import { useQuery } from '@apollo/client/react';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import GET_AUTHOR_BOOKS from './query';
import BookBlock from '../../components/BookBlock/BookBlock';
import Loader from '../../components/Loader/Loader';
import PageForm from '../../containers/PageForm/PageForm';
import { useLogin } from '../../contexts/LoginProvider';

const AuthorsBooks = () => {
   const { authorID } = useParams()
   const [openForm, setOpenForm] = useState(false)
   const [books, setBooks] = useState()
   const [dataAuthor, setDataAuthor] = useState()
   const [token] = useLogin()
   const { data, loading } = useQuery(GET_AUTHOR_BOOKS, {
      variables: {
         ID: authorID
      }
   })

   useEffect(() => {
      if (data && data?.author) {
         setBooks(data?.author?.data?.books)
         setDataAuthor(data?.author?.data)
      }
   }, [data])
   return (
      <div className="authorsBooks container">
         {
            token?.token && <PageForm
               openForm={openForm}
               setOpenForm={setOpenForm}
               info={dataAuthor}
               type="author"
            />
         }
         
         {
            loading && <Loader />
         }
         <div className="authorsBooks-top">
            <h2 className="authorsBooks-top__title">
               {dataAuthor?.authorName}
            </h2>

            {
               token?.token && <button className="authorsBooks-top__button"
                  onClick={() => setOpenForm(true)}
               >
                  Edit
               </button>
            }

            

         </div>
         <div className="authorsBooks-center">
            {
               books && books.map((el, key) => (
                  <BookBlock data={el} key={key} />
               ))
            }
         </div>
      </div>
   )
}

export default AuthorsBooks