import './VerifyLoader.scss';

const VerifyLoader = ({loading, verify}) => {
   
   return (
      <div className="verifyLoader">
        <div className="verifyLoader-inner">
           
            {
               loading && <div className="deleteModal-center">
                  <div className="lds-ring"><div></div><div></div><div></div><div></div></div>
               </div>
            }

            {
               verify && <div className='checkMark'>
                  <div className="success-checkmark">
                     <div className="check-icon">
                        <span className="icon-line line-tip"></span>
                        <span className="icon-line line-long"></span>
                        <div className="icon-circle"></div>
                        <div className="icon-fix"></div>
                     </div>
                  </div>
               </div>
            }

            <h3 className="verifyLoader-inner__title">
               {
                  verify ? "Tez orada siz bilan bog'lanamiz." :
                  "Wait"
               }
               
            </h3>
           
        </div>
      </div>
   )
}

export default VerifyLoader