import './assets/styles/main.scss';
import Header from "./containers/Header/Header";
import { Routes, Route } from 'react-router-dom';
import Home from "./pages/Home/Home";
import Footer from './containers/Footer/Footer';
import { useEffect, useState } from 'react';
import BookPage from './pages/BookPage/BookPage';
import AllBooks from './pages/AllBooks/AllBooks';
import Login from './containers/Login/Login';
import Admin from './pages/Admin/Admin';
import Search from './pages/Search/Search';
import AdminAuthors from './pages/AdminAuthors/AdminAuthors';
import AdminGenre from './pages/AdminGenre/AdminGenre';
import AuthorsBooks from './pages/AuthorsBooks/AuthorsBooks';
import Basket from './pages/Basket/Basket';
import GenreBooks from './pages/GenreBooks/GenreBooks';
import Popular from './pages/Popular/Popular';
import AboutUs from './pages/AboutUs/AboutUs';
import Authors from './pages/Authors/Authors';


const App = () => {
  const [ofset, setOfset] = useState(false)
  useEffect(() => {
    window.addEventListener('scroll', () => {
      if (window.pageYOffset > 1 && !ofset) {
        setOfset(true)
      } else {
        setOfset(false)
      }
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const token = window.localStorage.getItem('token')
  return (
    <div className="app">
      <div className={`app-header ${ofset ? 'active' : ''}`}>
        <Header />
      </div>

      <div className="app-center">
        <Routes>


          {
            token ?
              <>
                <Route path='/' element={<Admin />} /> :
                <Route path='/about/:bookID' element={<BookPage />} />
                <Route path='/author/:authorID' element={<AuthorsBooks />} />
                <Route path='/authors' element={<AdminAuthors />} />
                <Route path='/genres' element={<AdminGenre />} />
              </> :
              <>
                <Route path='/' element={<Home />} />
                <Route path='/about/:bookID' element={<BookPage />} />
                <Route path='/books' element={<AllBooks />} />
                <Route path='/login' element={<Login />} />
                <Route path='/author/:authorID' element={<AuthorsBooks />} />
                <Route path='/genre/:genreID' element={<GenreBooks />} />
                <Route path='/authors' element={<Authors />} />
                <Route path='/busket' element={<Basket />} />
                <Route path='/search' element={<Search />} />
                <Route path='/popular' element={<Popular />} />
                <Route path='/aboutus' element={<AboutUs />} />
              </>
          }
        </Routes>
      </div>

      <div className="app-footer">
        <Footer />
      </div>
      <div className="app-footer-aboutdev container">
        <a href="https://exadot.io/" className="app-footer-aboutdev"><span>exadot.io</span> tomonidan ishlab chiqilgan</a>
      </div>
    </div>
  );
}

export default App;
