import './MyModal.scss';

const MyModal = ({ setOpenModal, openModal, setFunction, loading }) => {
   return (
      <div className={`myModal ${openModal ? 'active' : ''}`}>
         <div className="myModal-inner">
            {
               loading && <span className='loading'></span>
            }
            <div className="myModal-inner-top">
               <h3>
                  Do you really want to delete ?
               </h3>
            </div>
            <div className="myModal-inner-center">
               <button className="myModal-inner-center__no"
                  onClick={() => setOpenModal(false)}
               >
                  No
               </button>
               <button className="myModal-inner-center__yes"
                  onClick={setFunction}
              >
                  Yes
               </button>
            </div>
         </div>
      </div>
   )
}

export default MyModal