import AuthorCard from '../../components/AuthorCard/AuthorCard';
import './AdminAuthors.scss';
import { GET_AUTHORS } from './query';
import { useQuery } from '@apollo/client/react';
import { useEffect, useState } from 'react';
import PageForm from '../../containers/PageForm/PageForm';
import Loader from '../../components/Loader/Loader';
import { useLogin } from '../../contexts/LoginProvider';

const AdminAuthors = () => {
   const {data, loading} = useQuery(GET_AUTHORS)
   const [arrData, setArrData] = useState()
   const [openForm, setOpenForm] = useState(false)
   const [token] = useLogin()
   const [count, setCount] = useState(0)

   useEffect(() => {
      if (data && data?.authors?.success) {
         setArrData(data?.authors?.data)
         setCount(data?.authors?.data?.length)
      }
   }, [data])
   
   return (
      <div className="adminAuthors container">
         {
            token?.token && <PageForm
               openForm={openForm}
               setOpenForm={setOpenForm}
               type="author"
            />
         }
         
         {
            loading && <Loader/>
         }
         <div className="adminAuthors-top">
            <h2 className="adminAuthors-top__title">
               Mualliflar ({count})
            </h2>

            {
               token?.token && <button className="adminBooks-top__button"
                  onClick={() => setOpenForm(true)}
               >
                  Author Add
               </button>
            }

           
         </div>

         <div className="adminAuthors-center">
            {
               arrData && arrData.map((el, key) => (
                  <AuthorCard elem={el} key={key} type={`author`} />
               ))
            }
         </div>
      </div>
   )
}

export default AdminAuthors