import { useQuery } from '@apollo/client/react';
import { useEffect, useState } from 'react';
import BookBlock from '../../components/BookBlock/BookBlock';
import Loader from '../../components/Loader/Loader';
import GET_POPULAR_BOOKS from './query';

const Popular = () => {
   const [books, setBooks] = useState()
   const { data, loading } = useQuery(GET_POPULAR_BOOKS, {
      variables: {
         count: 12,
         page: 1,
         popular: true
      }
   })

   useEffect(() => {
      if (data && data?.books?.success) {
         setBooks(data?.books?.data)
      }
   }, [data])
   return (
      <div className="authorsBooks container">
         {
            loading && <Loader />
         }
         <div className="authorsBooks-top">
            <h2 className="authorsBooks-top__title">
               Yuqori reytingli kitoblar
            </h2>

            
         </div>
         <div className="authorsBooks-center">
            {
               !loading ? books &&  books.map((el, key) => (
                  <BookBlock data={el} key={key} />
                  )) : <>
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
                  <BookBlock  />
               
               </>
            }
         </div>
      </div>
   )
}

export default Popular