import './FotoInput.scss';

const FotoInput = ({ setImg, img }) => {
   return (
      <div className="fotoInput">
         <div className="fotoInput-left">
            <img src={img.imgURL} alt="" />
         </div>
         <div className="fotoInput-right">
            <p className="fotoInput-right__subtitle">
               1. Rasim razmeri: eni = 495px; balandligi: 768px;<br /><br />
               2. Rasim minifikatsiya qilingan bo'lishi kerak
            </p>
            <label htmlFor="fotoInput">
               <input onChange={(e) => {
                  setImg({
                     imgURL: URL.createObjectURL(e.target.files[0]),
                     img: e.target.files[0]
                  })
               }} type="file" id="fotoInput" accept='.jpg, .jpeg, .png' />
            </label>
         </div>
      </div>
   )
}

export default FotoInput