import './GenreBooks.scss';
import { useQuery } from '@apollo/client/react';
import { useParams } from 'react-router-dom';
import { useEffect, useState } from 'react';
import BookBlock from '../../components/BookBlock/BookBlock';
import Loader from '../../components/Loader/Loader';
import GET_GENRE_BOOKS from './query';

const GenreBooks = () => {
   const { genreID } = useParams()
   const [books, setBooks] = useState()
   const [name, setName] = useState()
   const { data, loading } = useQuery(GET_GENRE_BOOKS, {
      variables: {
         ID: genreID
      }
   })

   useEffect(() => {
      if (data && data?.genre?.success) {
         setBooks(data?.genre?.data?.books)
         setName(data?.genre?.data?.genreName)
      }
   }, [data])
   return (
      <div className="GenreBooks container">
         {
            loading && <Loader />
         }
         <div className="GenreBooks-top">
            <h2 className="GenreBooks-top__title">
               {name && name.charAt(0).toUpperCase() + name.slice(1)}
            </h2>


         </div>
         <div className="GenreBooks-center">
            {
               books && books.map((el, key) => (
                  <BookBlock data={el} key={key} />
               ))
            }
         </div>
      </div>
   )
}

export default GenreBooks