import { useEffect, useState } from 'react';
import BookBlock from '../../components/BookBlock/BookBlock';
import PageForm from '../../containers/PageForm/PageForm';
import { GET_BOOKS } from '../Home/query';
import { useQuery } from '@apollo/client/react';
import './AdminBooks.scss';
import Loader from '../../components/Loader/Loader';

const AdminBooks = () => {
   const [openForm, setOpenForm] = useState(false)
   const [count, setCount] = useState(0)
   
   const { data, loading, refetch } = useQuery(GET_BOOKS, {
      variables: {
         search: '',
         page: 1,
         count: 1000
      }
   })
   const [sectionData, setSectionData] = useState()

   useEffect(() => {
      if (data) {
         setSectionData(data?.books?.data)
         setCount(data?.books?.data?.length)
      }
   }, [data])

   


   return (
      <div className="adminBooks">
         <PageForm
            openForm={openForm}
            setOpenForm={setOpenForm}
            setFunk={refetch}
            type="book"
         />
         {
            loading && <Loader/>
         }
        
         <div className="adminBooks-top">
            <h2 className="adminBooks-top__title">
               Books ({count})
            </h2>
            
            <button className="adminBooks-top__button"
               onClick={() => setOpenForm(true)}
            >
               Book Add 
            </button>
         </div>

         <div className="adminBooks-center">
            {
               sectionData && sectionData.map((el, key) => (
                  <BookBlock data={el} key={key}/>
               ))
            }
         </div>
      </div>
   )
}

export default AdminBooks