import './NotResult.scss';

const NotResult = ({text}) => {
   return (
      <div className="notResult">
         <div className="empty-state">
            <div className="empty-state__content">
               <div className="empty-state__icon">
                  <img src="data:image/png;base64," alt=""/>
               </div>
               <div className="empty-state__message">"{text}" bo'yicha hech narsa topilmadi</div>
               <div className="empty-state__help">
                  Agar sizga kerakli narsani topa olmasangiz adminlarimizga murajat qilishingiz mumkun.
               </div>
            </div>
         </div>

      </div>
   )
}

export default NotResult