import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import GET_GENRE_BOOKS from '../../pages/GenreBooks/query';
import BookSwiper from '../BookSwiper/BookSwiper';
import { useQuery } from '@apollo/client/react';
import './HomeBooks.scss';

const HomeBooks = ({ title, link }) => {
   const [books, setBooks] = useState()
   const { data, loading } = useQuery(GET_GENRE_BOOKS, {
      variables: {
         ID: link
      }
   })

   useEffect(() => {
      if (data && data?.genre?.success) {
         setBooks(data?.genre?.data?.books.slice(0, 11))
      }
   }, [data])

   return (
      <div className="homeBooks">
         <div className="homeBooks-top container">
            <h2 className="homeBooks-top__title">{title}</h2>
            <Link to={`/genre/${link}`} className="homeBooks-top__link">Hammasini ko'rish</Link>
         </div>
         <div className="homeBooks-center">
            <BookSwiper data={books} loading={loading}/>
         </div>
      </div>
   )
}

export default HomeBooks