import TextInput from '../../../components/Inputs/TextInput/TextInput';
import { useEffect, useState } from 'react'
import { useMutation } from '@apollo/client/react';
import { ADD_AUTHOR, UPDATE_AUTHOR } from './query';
import CommentInput from '../../../components/Inputs/CommentInput/CommentInput';
import Loader from '../../../components/Loader/Loader';

const AdminAddAuthor = ({ setOpenForm, info }) => {
   const [name, setName] = useState(info && info?.authorName)
   const [about, setAbout] = useState('a')


   const [createAuthor, { data: create, loading: createLoading }] = useMutation(ADD_AUTHOR)
   const [updateAuthor, { data: update, loading: updateLoading }] = useMutation(UPDATE_AUTHOR)

   const submit = (e) => {
      e.preventDefault()
      if(name && info?.Id){
         updateAuthor({
            variables: {
               ID: info?.Id,
               name: name,
               description: about
            }
         })
      }
      else if(!info?.Id && name){
         createAuthor({variables: {
            name: name,
            description: about
         }})
      }
   }

   useEffect(() => {
      if (create && create?.createAuthor?.success) {
         setOpenForm(false)
         window.location.reload()
      } else if (update && update?.updateAuthor?.success){
         setOpenForm(false)
         window.location.reload()
      }
   }, [create, setOpenForm, update])


   return (
      <div className="pageForm-right">
         {
            (createLoading || updateLoading) && <Loader/>
         }
         <div className="pageForm-right-top">
            <h3 className="pageForm-right-top__title">
               Add new Author
            </h3>

            <button className="pageForm-right-top__button"
               onClick={() => {
                  setOpenForm(false)
               }}
            >
               <span></span>
            </button>
         </div>
         <form className="pageForm-right-center"
            onSubmit={submit}
         >

            
            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                  Author name
               </label>
               <TextInput defaultInfo={name} setInfo={setName} />
            </div>

            <div className="pageForm-right-center-row">
               <label htmlFor="" className="pageForm-right-center-row__label">
                 Comment
               </label>
               <CommentInput defaultInfo={about} setInfo={setAbout} />
            </div>


            <button className='pageForm-right-center__button' type='submit'>Save</button>


         </form>
      </div>
   )
}

export default AdminAddAuthor