import AdminAddAuthor from '../Forms/AdminAddAuthor/AdminAddAuthor';
import AdminAddBook from '../Forms/AdminAddBook/AdminAddBook';
import AdminAddGenere from '../Forms/AdminAddGenere/AdminAddGenere';
import './PageForm.scss';

const PageForm = ({ openForm, setOpenForm, type, info, setFunk }) => {

   // useEffect(() => {
   //    if (!modal.classList.contains("hidden")) {
   //       // Disable scroll
   //       body.style.overflow = "hidden";
   //    } else {
   //       // Enable scroll
   //       body.style.overflow = "auto";
   //    }
   // }, [])

   return (
      <div className={`pageForm ${openForm ? 'active' : ''}`}>
         <div className="pageForm-left"
            onClick={() => setOpenForm(false)}
         ></div>
         {
            type === 'book' ?
               openForm && <AdminAddBook setOpenForm={setOpenForm} info={info} setFunk={setFunk}/>
            :
               type === 'author' ? openForm && <AdminAddAuthor setOpenForm={setOpenForm} info={info}/> :
                  openForm && <AdminAddGenere setOpenForm={setOpenForm}/>
         }
      </div>
   )
}

export default PageForm