import { useQuery } from '@apollo/client';
import { useEffect, useState } from 'react';
import BookBlock from '../../components/BookBlock/BookBlock';
import Loader from '../../components/Loader/Loader';
import NotResult from '../../components/NotResult/NotResult';
import { GET_BOOKS } from '../Home/query';
import './Search.scss';

const Search = () => {
   const [search, setSearch] = useState('')
   const [count, setCount] = useState(12)
   const { data, loading } = useQuery(GET_BOOKS, {
      variables: {
         search: search ,
         page: 1,
         count: count,
      }
   })
   const [sectionData, setSectionData] = useState([])

   useEffect(() => {
      if (data) {
         setSectionData(data?.books?.data)
      }
   }, [data])

   const submit = (e) => {
      e.preventDefault()
      if (e.target[0].value){
         setSearch(e.target[0].value)
      }
   }
   return (
      <div className="search container">
         {
            loading && <Loader/>
         }
         <form className="search-top" onSubmit={submit}>
            <input type="text" placeholder="Qidirish..."/>
            <button className="search-top__button" type='submit'>Qidirish</button>
         </form>
         
         <div className="search-center">
            {
               sectionData?.length ? sectionData.map((el, key) => (
                  <BookBlock data={el} key={key}/>
               )) : <NotResult text={search} />
            }
         </div>

         <div className="search-bottom">
            {
               sectionData?.length  ? <button className="search-bottom__button"
                  onClick={() => setCount((state) => state + 12)}
               >
                  Ko'proq...
               </button> : <></>
            }
            
         </div>
      </div>
   )
}

export default Search