import { gql } from "@apollo/client/core";

const GET_AUTHOR_BOOKS = gql`
   query author($ID: String!) {
      author(ID: $ID) {
         success
         message
         data {
            ...on Author {
               Id
               authorName
               books {
                  Id
                  bookName
                  bookPrice
                  bookImage
                  discount
                  author {
                     authorName
                  }
                  discount
               }

            }
         }
      }
   }
`

export default GET_AUTHOR_BOOKS