import {
   gql
} from "@apollo/client/core";

const ADD_AUTHOR = gql `
mutation createAuthor($name: String!,  $description: String!) {
   createAuthor(name: $name, description: $description) {
      success
      message
      data {
         ... on Author {
            Id
         }
      }
   }
}
`

const UPDATE_AUTHOR = gql `
mutation($ID: ID!, $name: String, $description: String) {
   updateAuthor(ID: $ID, name: $name, description: $description) {
      success
      message
   }
}
`

export {
   ADD_AUTHOR,
   UPDATE_AUTHOR
}