import { Link } from 'react-router-dom';
import './AuthorCard.scss';

const AuthorCard = ({elem, type}) => {
   const letter1 = elem?.authorName ? elem?.authorName?.split(' ')[0][0] : elem?.genreName?.split(' ')[0][0]
   return (
      <Link 
         to={`/${type}/${elem?.Id}`} 
         className="AuthorCard" >
         <div className="AuthorCard-img">
            <h2 className="AuthorCard-img__title">
               {letter1}
            </h2>
         </div>
         <div className="AuthorCard-info">
            <h4 className="AuthorCard-info__title">{elem?.authorName ? elem?.authorName : elem?.genreName}</h4>
            <span className="AuthorCard-info__subtitle"></span>
         </div>
      </Link>
   )
}

export default AuthorCard