import './AboutUs.scss';

const AboutUs = () => {
   return (
      <div className="aboutUs container">
         <h2 className="aboutUs__title">
            Biz haqimizda
         </h2>

         <p>
            "Ziyobooks" online kitob do'koni 2021-yil sentyabr oyida o'z faoliyatini boshladi. Online kitob do'koni dastlab Telegram, Instagramda savdo faoliyatini olib borgan.
         </p>

         <p>
            Ushbu loyihadan maqsad chet eldagi vatandoshlarimiz o'z din-u diyonati, qadriyatlari, tarixini yana ham ko'proq o'rganishi hamda chet ellarda farzandlarini haqiqiy musulmon qilib tarbiya qilishlariga yordam berish maqsadida ularga o'zbek, rus, ingliz tillardagi kitoblarni yetkazib berishdan iborat.
         </p>

         <p>
            Ziyobooks hozirda o'zining  "<a href="https://ziyobooks.com/">ziyobooks.com</a>" saytiga ega. Brooklynda savdo nuqtasi va Ohio shtatida hamkor do'koni mavjud.
         </p>

         <h3 className="aboutUs__subtitle">
            Bizning mahsulotlar:
         </h3>
         <ul>
            <li>O'zbek, rus, ingliz tilidagi kitoblar.</li>
            <li>Elektron mahsulotlar</li>
            <li>Plakatlar</li>
            <li>Hadyabop aksesuarlar</li>
         </ul>
      </div>   
   )
}

export default AboutUs