import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import './BookSwiper.scss';
import SwiperCore, { Autoplay } from 'swiper';
import { Pagination } from "swiper";
import BookBlock from "../../components/BookBlock/BookBlock";


SwiperCore.use([Autoplay]);

const BookSwiper = ({ data, loading }) => {
  return (
    <>
      <Swiper
        pagination={{
          clickable: true,
        }}
        modules={[Pagination]}
        className="mySwiper container"
        loop={true}
        autoplay={{
          delay: 2000,
        }}
        breakpoints={{
          640: {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          768: {
            slidesPerView: 3,
            spaceBetween: 30,
          },
          1024: {
            slidesPerView: 4,
            spaceBetween: 30,
          },
        }}
      >
        {/* max11 */}
        {/* bookName */}
        {/* bookPrice */}
        {
          !loading ?  data?.map((el) => (
            <SwiperSlide key={Math.random() * Math.random()}>
              <BookBlock data={el} />
            </SwiperSlide>
          )) : 
          <>
              <SwiperSlide>
                <BookBlock />
              </SwiperSlide>
              <SwiperSlide>
                <BookBlock />
              </SwiperSlide>
              <SwiperSlide>
                <BookBlock />
              </SwiperSlide>
              <SwiperSlide>
                <BookBlock />
              </SwiperSlide>
          </>
        }
      </Swiper>
    </>
  );
}

export default BookSwiper
