import { Link, NavLink } from 'react-router-dom';
import { BuyIcon, LoginIcon, SearchIcon } from '../../assets/Icons';
import './Header.scss';
import Logo from '../../assets/Images/Logo_black.png'
import { useState } from 'react';
import { useOrders } from '../../contexts/DataProvider';

const Header = () => {
    const [show, setShow] = useState(false)
    const [orders] = useOrders()

    const close = () => {
        setShow(false)
    }
    const token = window.localStorage.getItem('token')
    return (
        <div className="header container">
            <Link to={`/`} className="header-left">
                <img src={Logo} alt="" />
            </Link>
            <div className={`header-center ${show ? 'active' : ''}`}>
                {
                    !token ? <>
                        <NavLink to={`/`} onClick={close}>Asosiy</NavLink>
                        <NavLink to={`/authors`} onClick={close}>Mualliflar</NavLink>
                        <NavLink to={`/popular`} onClick={close}>Yuqori reytingli</NavLink>
                        <NavLink to={`/aboutus`} onClick={close}>Biz haqimizda</NavLink>
                    </> : <>
                        <NavLink to={`/`} onClick={close}>Books</NavLink>
                        <NavLink to={`/authors`} onClick={close}>Authors</NavLink>
                        <NavLink to={`/genres`} onClick={close}>Genre</NavLink>
                    </>
                }

                <div className="mobile">
                    {
                        token ?
                            <>
                                <Link to={`/`} className="button-login" onClick={() => {
                                    window.localStorage.removeItem('token')
                                    close()
                                }}>
                                    <span className="button-login__icon"><LoginIcon /></span> Chiqish
                                </Link>
                            </>
                            :
                            <>
                                
                                <Link to={`/login`} className="button-login" onClick={close}>
                                    <span className="button-login__icon"><LoginIcon /></span> Kirish
                                </Link>
                            </>
                    }
                </div>


            </div>

            <div className="header-right">
                {
                    token ?
                        <>
                            <Link to={`/`} className="button-login" onClick={() => {
                                window.localStorage.removeItem('token')
                                window.location = '/'
                            }}>
                                <span className="button-login__icon"><LoginIcon /></span> Chiqish
                            </Link>
                        </>
                        :
                        <>
                            <Link to={`/search`} className="button-search">
                                <SearchIcon />
                            </Link>
                            <Link to={`/busket`} className="button-search">
                                <BuyIcon />
                                {
                                    orders?.length > 0 && <i className="w3-alert-loader"></i>
                                }

                            </Link>
                            <Link to={`/login`} className="button-login">
                                <span className="button-login__icon"><LoginIcon /></span> Kirish
                            </Link>
                        </>
                }

            </div>

            <div className="boxHeader">
                <Link to={`/search`} className="button-search basket" onClick={close}>
                    <span className="button-login__icon"><SearchIcon /> </span>
                </Link>
                <Link to={`/busket`} className="button-search basket">
                    <BuyIcon />
                    {
                        orders?.length > 0 && <i className="w3-alert-loader"></i>
                    }

                </Link>

                <div className={`burger ${show ? 'active' : ''}`}
                    onClick={() => {
                        setShow(!show)
                    }}
                >
                    <span></span>
                </div>
            </div>

        </div>
    )
}

export default Header