import { useState } from "react"


const CommentInput = ({ defaultInfo, setInfo, features }) => {
   const [text, setText] = useState('')
   return (
      features ?

         <div className="input type2">
            <textarea cols="30" rows="5" onInput={(e) => setText(e.target.value)} value={text}></textarea>
            <button className="type2Button"
               type="button"
               onClick={() => {
                  setInfo((info) => [...info, text])
                  setText('')
               }}
            >
               +
            </button>
         </div>

         :

         <div className="input type1">
            <textarea cols="30" rows="10" defaultValue={defaultInfo}  onInput={(e) => setInfo(e.target.value)}  maxLength={600}></textarea>
         </div>
   )
}


export default CommentInput