import {
   gql
} from "@apollo/client/core";

const GET__BOOK = gql `
query book($ID: ID!) {
   book(ID: $ID) {
      success
      message
      data {
         ...on Book {
            Id
            bookName
            bookPrice
            bookDescription
            bookRelease
            bookPages
            bookWeight
            bookImage
            publishHouse
            author {
               Id
               authorName
            }
            genre {
               Id
               genreName
            }
            bookExcerpt
            discount
            forWhom
            format
         }
      }
   }
}
`

const DELETE_BOOK = gql`
  mutation deleteBook($ID: ID!) {
     deleteBook(ID: $ID) {
        success
        message
     }
  }
`

export  {
   GET__BOOK,
   DELETE_BOOK
}