import './Login.scss';
import Foto from '../../assets/Images/loginimg.png';
import { useMutation } from '@apollo/client';
import { LOGIN } from './query';
import { useEffect } from 'react';

const Login = () => {
   const [login, {data}] = useMutation(LOGIN)
   // admin1
   const submit = (e) => {
      e.preventDefault()

      login({variables: {
         username: e.target[0].value,
         password: e.target[1].value
      }})
   }

   useEffect(() => {
      if(data && data?.login?.message === "You are successfully logged!") {
         window.localStorage.setItem("token", data?.login?.data?.token)
         window.location = '/'
      }
   }, [data])


   return (
      <div className="login container">
         <form className="login-left" onSubmit={submit}>
            <h4 className="login-left__title">Kirish</h4>
            <input type="text" className="login-left__input" placeholder='Email' />
            <input type="password" className="login-left__input" placeholder='Parol'/>
            <button className="login-left__button">
               Kirish
            </button>
         </form>
         <div className="login-right">
            <img src={Foto} alt="" />
         </div>
      </div>
   )
}

export default Login