import './BookPage.scss';
import Book from '../../assets/Images/anim.png'
import { useEffect, useState } from 'react';
import { useParams } from 'react-router-dom'
import Loader from '../../components/Loader/Loader';
import { DELETE_BOOK, GET__BOOK } from './query';
import {
   useQuery,
   useMutation
} from '@apollo/client/react';
import PageForm from '../../containers/PageForm/PageForm';
import MyModal from '../../containers/MyModal/MyModal';
import { useOrders } from '../../contexts/DataProvider';
import { BuyIcon } from '../../assets/Icons';

const BookPage = () => {
   const token = window.localStorage.getItem('token')
   const { bookID } = useParams()
   const [info, setInfo] = useState()
   const [openForm, setOpenForm] = useState(false)
   const [openModal, setOpenModal] = useState(false)
   const [orders, setOrders] = useOrders()
   const [countOrder, setCountOrder] = useState(0)





   const [deleteBook, { data: delData, loading: delLoading }] = useMutation(DELETE_BOOK)

   const { data, loading, refetch } = useQuery(GET__BOOK, {
      variables: {
         ID: bookID
      }
   })
   useEffect(() => {
      window.scrollTo(0, 0)
   }, [])

   useEffect(() => {
      if (data) {
         setInfo(data?.book?.data)
      }
   }, [data])

   const deleteFunction = () => {
      deleteBook({
         variables: {
            ID: info?.Id
         }
      })
   }

   useEffect(() => {
      if (delData?.deleteBook?.success) {
         setOpenModal(false)
         window.location = '/'
      }
   }, [delData])


   useEffect(() => {
      if (orders?.length > 0 && bookID) {
         let element = orders.find(el => el?.Id === bookID)
         if (element) {
            setCountOrder(element?.count)
         }
      }
   }, [orders, bookID])

   return (
      <div className="bookPage container">
         <MyModal
            openModal={openModal}
            setOpenModal={setOpenModal}
            setFunction={deleteFunction}
            loading={delLoading}
         />
         {
            loading && <Loader />
         }

         {
            info && <PageForm
               openForm={openForm}
               setOpenForm={setOpenForm}
               setFunk={refetch}
               info={info}
               type="book"
            />
         }
         <div className="bookPage-top">
            <h2 className="bookPage-top__title">
               Kitob <span>haqida</span>
            </h2>

            {
               token && <div className="bookPage-top-row">
                  <button type='button' className="bookPage-top__button" onClick={() => setOpenForm(true)}>Edit</button>
                  <button type='button' className="bookPage-top__button red" onClick={() => setOpenModal(true)}>Delete</button>
               </div>
            }

         </div>

         <div className="bookPage-center">
            <div className="bookPage-center-row">
               <div className="bookPage-center-row-left">
                  <img src={info?.bookImage ? info?.bookImage : Book} alt="" />
               </div>

               <div className="bookPage-center-row-right">
                  <h3 className="bookPage-center-row-right__title">
                     {info?.bookName || "Book name"}
                  </h3>
                  <span className="bookPage-center-row-right__subtitle">
                     {info?.author?.authorName || "Author name"}
                  </span>
                  <div className="buy">
                     <h4 className="bookPage-center-row-right__price">
                        {
                           info?.discount > 0 && <del>${info?.bookPrice}</del>
                        }
                        ${(info?.discount > 0 ? +info?.bookPrice - +info?.discount : info?.bookPrice) || "Price"}
                     </h4>

                     <button type='button' className={`bookPage-center-row-right__button ${countOrder === 0 && 'active'}`}
                        onClick={() => {
                           setOrders((state) => {
                              let find = state.find(el => el.Id === info?.Id)

                              if (find) {
                                 let array = []
                                 find.count = find.count + 1
                                 state.forEach(el => {
                                    if (el.Id === info?.Id) {
                                       array.push(find)
                                    } else {
                                       array.push(el)
                                    }
                                 })
                                 return array
                              } else {
                                 return [...state, {
                                    ...info,
                                    count: 1
                                 }]
                              }

                           })
                        }}
                     >
                        <BuyIcon />
                        {
                           countOrder > 0 && <i className="w3-alert-loader">{countOrder} </i>
                        }

                     </button>
                  </div>
                  <div className="bookPage-center-row-right-about">
                     <h5 className="bookPage-center-row-right-about__title">
                        {info?.bookDescription && `Nima haqida`}
                     </h5>

                     <p className="bookPage-center-row-right-about__text">
                        {info?.bookDescription || "Lorem ipsum dolor sit amet consectetur adipisicing elit. Dolore deserunt dolorem iure quia illo? Esse, dicta! Voluptas vitae dolorum magnam illum enim blanditiis corrupti nam dignissimos eveniet accusamus aperiam totam, ipsum iure voluptatum a. Eum voluptate sapiente voluptatum commodi deleniti provident, earum molestias blanditiis! Autem aut vero iusto aperiam modi. Veniam esse eos eveniet quas doloribus, hic porro quod accusamus maxime velit quae accusantium alias! Neque libero repellat animi, deserunt veniam nam quaerat itaque dolores minus ab soluta, totam repudiandae officiis tempore vero? Soluta corrupti quaerat, esse nam ad quod expedita doloremque eum vitae veritatis explicabo ipsam reprehenderit et dolores!"}
                     </p>
                  </div>

                  {
                     info?.bookExcerpt  ?
                        <div className="bookPage-center-row-right-discr">
                           <p>
                              {info?.bookExcerpt}
                           </p>

                        </div> : <></>
                  }
               </div>


            </div>

            <div className="bookPage-center-row">
               <div className="bookPage-center-row-left row2">
                  <div className="bookPage-center-row-left-box block">
                     <h4 className="title">Kitob nomi</h4>
                     <h4 className="subtitle">{info?.bookName}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box block">
                     <h4 className="title">Muallif ismi</h4>
                     <h4 className="subtitle">{info?.author?.authorName}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Sahifalar soni</h4>
                     <h4 className="subtitle">{info?.bookPages || `No data`}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Janr</h4>
                     <h4 className="subtitle">{info?.genre?.genreName}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Nashriyot nomi</h4>
                     <h4 className="subtitle">{info?.publishHouse || `No data`}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Chop etilgan yili</h4>
                     <h4 className="subtitle">{info?.bookRelease || `No data`}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Formati</h4>
                     <h4 className="subtitle">{info?.format || `No data`}</h4>
                  </div>
                  <div className="bookPage-center-row-left-box">
                     <h4 className="title">Massa</h4>
                     <h4 className="subtitle">{info?.bookWeight > 0 ? `${info?.bookWeight} g.` : `No data`}</h4>
                  </div>
               </div>

               <div className="bookPage-center-row-right row2">
                  {/* <div className="bookPage-center-row-right-feature">
                     <h3 className="bookPage-center-row-right-feature__title">
                        Features
                     </h3>

                     <ul>
                        <li>For several decades, this philosophical novel has remained on the world's bestseller
                           list and has become a classic for millions of readers.
                        </li>
                        <li>
                           The plot is fascinating and unpredictable, and the philosophical ideas are
                           presented clearly and simply.
                        </li>

                        <li>
                           Reading the "Source" will help you to truly understand the ideas of the novel
                           "Atlas Shrugged", as well as the philosophical and nonfiction books of Ayn Rand.
                        </li>
                     </ul>
                  </div> */}

                  <div className="bookPage-center-row-right-forwhom">
                     <h4 className="bookPage-center-row-right-forwhom__title">
                        {
                           info?.forWhom  && `Kim uchun`
                        }
                     </h4>

                     <p className="bookPage-center-row-right-forwhom__text">
                        {
                           info?.forWhom && info?.forWhom
                        }
                     </p>
                  </div>
               </div>


            </div>
         </div>
      </div>
   )
}

export default BookPage