import { useEffect, useState } from 'react';
import { DeleteIcon } from '../../assets/Icons';
import './BasketBlock.scss';
import BookAnim from '../../assets/Images/anim.png'
import { useOrders } from '../../contexts/DataProvider';

const BasketBlock = ({ data }) => {
   const [ setOrder] = useOrders(true)
   const [counter, setCounter] = useState(data?.count ? data?.count : 1)


   useEffect(() => {
      if (counter > 0) {
         setOrder((state) => {
            let find = state.find(el => el.Id === data?.Id)
            let array = []
            find.count = counter
            state.forEach(el => {
               if (el.Id === data?.Id) {
                  array.push(find)
               } else {
                  array.push(el)
               }
            })
            return array

         })
      }
      // eslint-disable-next-line react-hooks/exhaustive-deps
   }, [counter, setOrder])

   useEffect(() => {
      setCounter(data?.count)
   }, [data])
   return (
      <div className="basketBlock">

         <div className="basketBlock-left">
            <div className="basketBlock-left-img">
               <img src={data?.bookImage ? data?.bookImage : BookAnim} alt="" />
            </div>
            <div className="basketBlock-left-info">
               <h3 className="basketBlock-left-info__title">{data?.bookName}</h3>
               <span className="basketBlock-left-info__subtitle">{data?.author?.authorName}</span>
            </div>
         </div>


         <div className="basketBlock-right">
            <button className="basketBlock-right__delete"
               onClick={() => {
                  setOrder((state) => {
                     let array = []

                     state.forEach(el => {
                        if (el !== data) {
                           array.push(el)
                        }
                     })

                     return array
                  })
               }}
            >
               <DeleteIcon />
            </button>
            <h3 className="basketBlock-right__price">${data?.bookPrice * counter}</h3>
            <div className="basketBlock-right-counter">
               <button className="basketBlock-right-counter__button"
                  onClick={() => {
                     setCounter((state) => state - 1 === 0 ? 1 : state - 1)
                  }}
                  disabled={counter <= 1}
               >
                  <svg width="14" height="2" viewBox="0 0 14 2" xmlns="http://www.w3.org/2000/svg">
                     <path d="M0.583984 0.0820312H13.4173V1.91536H0.583984V0.0820312Z" />
                  </svg>
               </button>

               <h3 className="basketBlock-right-counter__title">{counter}</h3>

               <button className="basketBlock-right-counter__button"
                  onClick={() => {
                     setCounter((state) => state + 1)
                  }}
               >
                  <svg width="14" height="14" viewBox="0 0 14 14" xmlns="http://www.w3.org/2000/svg">
                     <path d="M6.08399 6.08203V0.582031H7.91732V6.08203H13.4173V7.91537H7.91732V13.4154H6.08399V7.91537H0.583984V6.08203H6.08399Z" />
                  </svg>
               </button>
            </div>


         </div>
      </div>
   )
}


export default BasketBlock